<template>
    <div class="receivables main-content">
        <mt-header class="account-header" fixed title="">
            <mt-button @click="$router.history.go(-1)" slot="left" icon="back"></mt-button>
        </mt-header>
        <VueQArt class="address-qa" :config="config"></VueQArt>
        <p id="extension" style="margin-top:30px;font-size:12px;text-align:center;color:#fff;">
            12Z6Pjuu8XsdaHdfg54adka0349jlhk
        </p>
        <button @click="cpaddress" class="cp-address" data-clipboard-target="#extension"></button>
        <Alert :ModelOptions="confirmOptions" ref="cp_notice"></Alert>
    </div>
</template>

<script>
import VueQArt from 'vue-qart';
import Clipboard from 'clipboard';
import Alert from '../com/alert.vue';

export default {
    components: {VueQArt,Alert},
    data(){
        return {
            config: {
                value: 'http://localhost:8080/#/receivables',
                imagePath: '/static/img/qa-bg.png',
                filter: '#fff'
            },
            confirmOptions:{

            }
        }
    },
    mounted(){
        
    },
    methods:{
        cpaddress(){
            let that = this;
            var clipboard = new Clipboard('.cp-address');
            clipboard.on('success', function(e) {
                console.info('Action:', e.action);
                console.info('Text:', e.text);
                console.info('Trigger:', e.trigger);
                e.clearSelection();
                that.$refs.cp_notice.showModel();
                that.confirmOptions= {
                    img:false,// imgsrc
                    //imgsrc:'',//
                    type: "1",// 1: 2:textarea inputvalue,readonly
                    // inputvalue:"qwr124f1wt24tef34t235g23yj4fqwf567f23fgtrbqewftyn43btn",//textarea
                    // readonly:false,// 
                    title: "",//
                    message: "",//
                    btnText: "",//
                    notice:"",//   
                    btnCancelFunction: function (val) {//
                        console.log(that.confirmOptions.inputvalue)
                        // alert(val)
                    }
                }
            });
            clipboard.on('error', function(e) {
                console.error('Action:', e.action);
                console.error('Trigger:', e.trigger);
            });

        }
    }
}
</script>

<style lang="less">
.receivables{
    padding:40px 20px;
    position: relative;
    .account-header{
        background: none;
    }
    .address-qa{
        text-align: center;
        margin-top:10rem;
    }
    .cp-address{
        position:absolute;
        bottom:5rem;
        left:20px;
        width:calc(100% - 40px);
        border:none;
        height:5rem;
        font-size:16px;
        border-radius: 50px;
        background-image:linear-gradient(131deg, #fc5d9f 0%, #5863fc 100%);
        color:#fff;
    }
    @media screen and (max-width: 320px) {
        .address-qa{
            text-align: center;
            margin-top:5rem;
        }
    }
}
    
</style>


